#ju_bbox{
  display: none!important;
}


.c-cart-upsells {
  padding: 0 1.25rem 0;

  * {
    font-size: .9375rem;
  }

  &__inner {
    padding: 1.5rem 0 2rem;
    border-top: .0625rem solid $s-color--border-20;
    border-bottom: .0625rem solid $s-color--border-20;
  }

  &__products {
    display: flex;
    flex-direction: column;
    gap:1rem;
  }

  &__product {
    display: grid;
    grid-template-columns: 4rem 1fr;
    grid-gap: .875rem;
    align-items: center;
  }

  &__image-container {
    width: 4rem;
    padding-bottom: 4rem;
  }

  &__image {
    object-fit: contain;
  }

  &__add-button {
    display: flex;
    align-items: center;
    gap: .25rem;
    cursor: pointer;
  }

  &__add-icon{
    width: 1.1875rem;
    height: 1.1875rem;
  }

  &__details-wrapper{
    display: flex;
    justify-content: space-between;
    align-items: start;
    position: relative;
  }
  &__details {
    p {
      margin: 0;
    }
  }

  &__message {
    color: $s-color--font-subdued-40;
  }

  &__tooltip-label{
    color: $s-color--font-focus;
    display: block;
    cursor: pointer;

    &:hover + .c-cart-upsells__tooltip{

      opacity: 1;
      visibility: visible;
      pointer-events: all;
    }
  }

  &__tooltip{
    position: absolute;
    bottom: 2.5rem;
    left: 0;
    width: 15rem;
    border-radius: .625rem;
    background-color: $s-color--background;
    box-shadow: 0rem .625rem .9375rem rgba($s-color--border,.3);
    z-index: $s-z--cart-drawer-tooltip;
    padding: 1rem;
    pointer-events: none;
    visibility: hidden;
    opacity: 0;
    transition: opacity .2s;
    font-size: .75rem;
    font-weight: 400;

  }

  &__description{
    color: $s-color--font-subdued-40;
    font-size: .75rem;
  }

  &__price-wrapper{
    display: flex;
    align-items: center;
    gap: .5rem;
  }

  &__compare-price{
    display: inline-block;
    color: $s-color--font-50;
    text-decoration: line-through;
    margin-right: .25rem;
  }

  @include t-media-query($s-small-up) {
    &__inner {
      padding-top: 1.5rem;
      padding-right: 3.375rem;
      padding-bottom: 2rem;
      padding-left: 2.5625rem;
    }
  }
}
