.c-cart-progress {
  display: none;

  .is-location-au &[data-country-code="au"] {
    display: block;
  }

  .is-location-int &[data-country-code="international"] {
    display: block;
  }

  &__progress-bar {
    width: 100%;
    border-radius: 0.25rem;
    background: rgba($s-color--background, 0.25);
    height: 0.25rem;
    display: block;
    position: relative;
    overflow: hidden;
    margin-bottom: 0.875rem;
  }

  &__progress-fill {
    position: absolute;
    top: 0;
    height: 0.25rem;
    background: $s-color--background;
  }

  &__label {
    margin-bottom: 0.125rem;
  }

  @include t-media-query($s-small-up) {
    &__progress-fill {
      background: $s-color--btn-background;
    }

    &__progress-bar {
      background: rgba($s-color--btn-background, 0.25);
    }

    &__label {
      margin-bottom: .3125rem;
    }
  }
}