.c-cart-contents {
  &__inner {
    overflow: scroll;
    scrollbar-width: none;
    scrollbar-color: $s-color--background $s-color--background;
    padding-bottom:11rem; // Size of the sticky footer

    .c-cart-template & {
      padding-bottom: 0;
      max-width: 37.5rem;
      margin: 0 auto;
    }

    &::-webkit-scrollbar {
      width: 0;
      height: 0;
      display: none;
    }

    &::-webkit-scrollbar-track {
      background: $s-color--background;
      height: 0;
      width: 0;
    }

    &::-webkit-scrollbar-thumb {
      background: $s-color--background;
      height: 0;
      width: 0;
    }
  }

  &__header {
    background-color: $s-color--background-subdued;
    padding: .75rem 1.125rem .5rem;
  }

  &__title-wrapper {
    display: flex;
    justify-content: space-between;
    margin-bottom: 0.625rem;
  }

  &__title {
    margin: 0 auto;
    display: flex;
  }

  &__count {
    margin-left: 0.75rem;
    color: rgba($s-color--font, 0.4);
  }

  &__close {
    height: 1.5rem;
    width: 1.5rem;
    cursor: pointer;


    .c-cart-template & {
      display: none;
    }
  }

  &__close-icon {
    height: 100%;
  }

  &__promo-wrapper {
    text-align: center;
  }

  &__promo-line-1 {
    margin-bottom: 0.125rem;
    display: none;
  }

  &__promo-line-2 {
    color: rgba($s-color--black, 0.4);
    margin-bottom: 0;
    font-size: 0.75rem;
  }

  &__footer {
    background: $s-color--background-impact;
    color: $s-color--font-invert;
    border-radius: 1.875rem 1.875rem 0 0;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    padding-bottom: 0;

    .c-cart-template & {
      position: relative;
    }
  }

  &__actions {
    padding: 1rem 1.5rem 1.125rem;
    width: 100%;
  }

  &__subtotal {
    width: 100%;
    font-weight: 500;
    display: flex;
    justify-content: space-between;
    margin-bottom: 0.25rem;
  }

  &__payment-icons {
    display: none;
    justify-content: center;
    align-items: center;
    gap: 1.5rem;
    opacity: .6;
  }

  &__payment-icon {
    cursor: pointer;

    svg {
      height: 0.9375rem;
      width: auto;

      path {
        fill: $s-color--background;
      }
    }
  }

  &__empty {
    text-align: center;
    padding: 2.5rem;
  }

  &__continue {
    text-align: center;
    margin-bottom: 2.5rem;
  }

  &__products {
    padding: 1rem 1rem 1.5rem;
    display: grid;
    grid-gap: 1.5rem;
  }
  

  &__footer-inner {
    max-width: 37.5rem;
    margin: 0 auto;
  }

  @include t-media-query($s-small-up) {
    &__inner {
      padding-bottom: 16.125rem;
    }

    &__actions {
      padding: 0;
      margin-bottom: 1.5rem;
    }

    &__footer {
      background: $s-color--background-subdued;
      color: $s-color--font-focus;
      border-radius: 0;
      padding: 1.125rem 2rem 0;
    }

    &__btn.is-alternative {
      background-color: $s-color--btn-background;
      color: $s-color--btn-font;
    }

    &__subtotal {
      color: $s-color--font;
    }

    &__header {
      padding: .5rem 2.375rem;
    }

    &__promo-line-2 {
      font-size: 0.9375rem;
    }

    &__products {
      padding: 1.9375rem 2.375rem 1.5rem;


      .c-cart-template & {

        padding: 3rem 2.375rem 1.5rem;
      }
    }

    &__payment-icon {
      cursor: pointer;

      svg {
        height: 0.9375rem;
        width: auto;

        path {
          fill: $s-color--background-impact;
        }
      }
    }
  }

  @include t-media-query($s-medium-up) {
    .is-cart-drawer-desktop {
      .c-cart-template & {
        display: none;
      }
    }

    .c-cart-template & {
      display: grid;
      grid-template-rows: min-content min-content;
      grid-template-columns: 1fr max(27rem, 32%);
      grid-column-gap: 5%;
      max-width: min(85%, 83.375rem);
      margin: 0 auto;
      padding-bottom: 12.5rem;


      &__inner {
        max-width: initial;
        width: 100%;

        &.is-empty {
          padding: 5rem;
          grid-column: span 2;
        }
      }

      &__header {
        grid-column: span 2;
        width: 31rem;
        justify-self: center;
        margin: 5.0625rem 0 3.4375rem;
        border-radius: 1rem;
      }

      &__footer {
        background-color: initial;
        padding-top: 0;
        padding-left: 0;
        padding-right: 0;
      }

      &__title-wrapper {
        margin-bottom: 0;
      }

      &__payment-icons {
        filter: saturate(0) brightness(0.6);
      }

      &__products {
        padding-left: 0;
        padding-right: 0;
      }

      &__notes {

        border-top: 0.0625rem solid $s-color--border-subdued-20;
      }

      .c-cart-upsells {
        padding: 0;

        &__inner {
          border: none;
          padding-left: 2.5rem;
          padding-right: 2.5rem;
        }
      }
    }
  }

  @include t-media-query($s-xlarge-up) {
    .c-cart-template & {

      .c-cart-upsells {
        padding: 0;

        &__inner {
          max-width: initial;
        }

        &__products {
          flex-direction: row;
          justify-content: space-between;
          gap: 2rem;
        }

        &__product {
          align-items: start;
          width: 100%;
        }
      }
    }
  }
}