/*
 *  Cart Product
 *    Styles for cart products. 
 *
 *
 *  Version
 *    1.0.0 - 2021/10/01
 */

$c-cart-product--border: 0.0625rem solid $s-color--border;

.c-cart-product {
  display: grid;
  grid-template-columns: min-content 1fr min-content;
  grid-template-rows: 1fr 1fr;
  grid-column-gap: 1.25rem;

  &__title,
  .o-input {
    font-size: 0.875rem;
    background-color: transparent;
  }

  &__image-container {
    width: 4.5rem;
    padding-bottom: 4.5rem;
    grid-row: span 2;

    &.non-clickable {
      pointer-events: none;
    }
  }

  &__image {
    @supports (object-fit: contain) {
      object-fit: contain;
    }
  }

  &__price-container {
    grid-column: 3/4;
    grid-row: 2/3;
    padding: 0.1875rem;
    display: flex;
  }

  &__remove {
    grid-column: 3/4;
    grid-row: 1/2;
  }

  .o-quantity-selector {
    &__inner {
      background-color: transparent;
      width: 4.375rem;
      padding: 0.25rem;
    }

    &__button {
      padding: 0;
    }
  }

  &__qty {
    display: block;
  }

  &__details {
    grid-row: span 2;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__remove {
    .is-cart-pending & {
      pointer-events: none;
    }
  }

  &__remove-link {
    display: block;
    padding: 0 0 0.5rem 0.5rem;
  }

  &__remove-icon {
    width: 1rem;
    margin: 0;
    margin-left: auto;
  }

  &__price-container {
    font-size: 0.875rem;
    margin: 0;
    align-self: end;
  }

  .o-pricing__compare {
    display: none;
  }

  &__compare-price {
    display: inline-block;
    color: $s-color--font-50;
    text-decoration: line-through;
    margin-right: .25rem;
  }

  &__title {
    margin: 0;
  }

  @include t-media-query($s-small-up) {

    &__title,
    .o-input {
      font-size: 0.9375rem;
    }

    &__image-container {
      width: 5rem;
      padding-bottom: 5rem;
    }

    .o-quantity-selector__inner {
      background-color: transparent;
      width: 3.6875rem;
      padding: 0.25rem 0;
    }

    &__remove-text{
      color: $s-color--font-subdued-40;
    }
  }
}