.c-cart-notes {
  padding: 1.5rem 1.25rem;

  &__accordion {
    margin-bottom: 1.75rem;

    &-input {
      background-color: $s-color--background-subdued;
      width: 100%;
      height: 5rem;
      padding: 0.75rem 1.3125rem;
    }

    &-heading {
      position: relative;
      cursor: pointer;
      &::marker {
        content: "";
      }

      &::-webkit-details-marker {
        // hide on Safari
        display: none;
      }

      &::after {
        @include t-anim--transition(transform, $s-anim--time-short, linear);
        @include t-absolute-center-y;
        right: 0;
        display: block;
        width: 1rem;
        height: 1rem;
        background: url(../../assets/icon-caret-down.svg) no-repeat center
          center;
        content: "";
        transform: translateY(-50%) rotate(-180deg);
      }
    }

    &[open] &-heading::after {
      transform: translateY(-50%);
    }

    &-message{
      margin: 1.0625rem 0 1.5rem;
      display: inline-block;
    }

    &-checkbox{
      margin-right: .5rem;
      accent-color: $s-color--btn-background;
    }

    &-footnote{
      font-size: .75rem;
      font-weight: 400;
    }
  }

  label.c-cart-notes__accordion-message{
    cursor: pointer;
  }


  @include t-media-query($s-small-up) {
    padding: 1.5rem 1.8125rem;

    &__accordion{
      margin-bottom: 1.125rem;
    }
  }
}
