.c-cart-drawer {

  position: fixed;
  display: flex;
  flex-direction: column;
  top: 0;
  right: -100%;
  width: 100%;
  height: 100%;
  max-width: 31.375rem;
  z-index: $s-z--cart-drawer;
  overflow: hidden;
  background: $s-color--background;
  @include t-anim--transition(right, $s-anim--time-med);


  &.is-drawer-open {
    right: 0;

    +.c-cart-drawer__background {
      width: 100%;
      opacity: 0.4;
      z-index: $s-z--drawer-background;
      transition: opacity $s-anim--time-med $s-anim--ease-out 0.3s,
        width 0s $s-anim--ease-out;
      pointer-events: auto;
      transform: translateZ(0);
    }
  }

  &__background {
    cursor: pointer;
    position: fixed;
    background: $s-color--black;
    right: 0;
    top: 0;
    width: 0;
    height: 100%;
    opacity: 0;
    transition: opacity $s-anim--time-med $s-anim--ease-out,
      width 0s $s-anim--ease-out $s-anim--time-med;
    pointer-events: none;
  }

  .is-cart-page{
    display: none;
  }

  @include t-media-query($s-medium-up) {
    .is-cart-page{
      display: none;
    }
  }
}