.c-cart-template {

  .c-cart-contents {

    &__header {
      padding-top: 1.25rem;
      padding-bottom: 1.25rem;
    }

    &__title.is-cart-page {
      display: none;
    }

    &__right {
      display: none;
    }
  }

  .c-cart-product {
    &__remove-text {
      display: none;
    }
  }


  @include t-media-query($s-medium-up) {

    .c-section__cart-drawer--inner {
      display: grid;
      grid-template-rows: min-content min-content;
      grid-template-columns: 1fr max(27rem, 32%);
      grid-column-gap: 5%;
      max-width: min(85%, 83.375rem);
      margin: 0 auto;
      padding-bottom: 12.5rem;
    }

    .c-cart-contents {
      &__title.is-cart-page {
        display: block;
        font-size: 1.5rem;
      }

      &__count {
        font-size: 1.125rem;
      }

      &__promo-line-1 {
        @include t-font--paragraph-2;

      }

      &__promo-line-2 {
        font-size: 0.75rem;
      }

      &__subtotal {
        font-size: 1.125rem;
      }

      &__right {
        display: block;
      }
    }

    .c-cart-notes {
      padding-left: 0;
      padding-right: 0;
    }

    .is-cart-drawer-desktop {
      display: none;
    }

    .c-cart-product {

      grid-template-columns: min-content 1fr repeat(2, min-content);
      grid-column-gap: 5%;
      grid-template-rows: 1fr;
      align-items: center;

      &__title,
      &__price-container {
        font-size: 0.875rem;
      }

      .o-input {
        font-size: 0.9375rem;
      }

      &__details {
        flex-direction: row;
        grid-gap: 1rem;
        align-items: center;
      }

      &__remove {
        grid-column: 4/5;
      }

      &__price-container {
        grid-column: 3/4;
        grid-row: initial;
        align-self: initial;
        flex-direction: column;
      }

      &__compare-price {
        margin: 0;
      }

      &__remove-link {
        padding: 0;
      }
    }
  }

  @include t-media-query($s-large-up) {

    .c-section__cart-drawer--inner {
      grid-column-gap: 8%;
    }

    .c-cart-product {

      &__remove-text {
        display: block;
        font-size: 0.875rem;
      }

      &__remove-icon {
        display: none;
      }
    }

    .c-cart-upsells {
      padding: 0;

      &__inner {
        border: none;
        padding-left: 2.5rem;
        padding-right: 0;
        max-width: initial;
      }

      &__products {
        flex-direction: row;
        justify-content: space-between;
        gap: 2rem;
      }

      &__product {
        align-items: start;
        width: 100%;
      }
    }
  }

  @include t-media-query($s-xlarge-up) {

    .c-section__cart-drawer--inner {
      grid-column-gap: 14%;
    }

    .c-cart-upsells {
      padding: 0;
    }
  }
}